import React, { Component } from "react";

class Cart extends Component {
  render() {
    return (
      <section className="section">
        <div className="table-responsive">
          <table className="table product-table">
            <thead>
              <tr>
                <th />
                <th>Sn Phm</th>
                <th>Gi</th>
                <th>S Lng</th>
                <th>Tng Cng</th>
                <th />
              </tr>
            </thead>
            <tbody>
              {this.props.children}
            </tbody>
          </table>
        </div>
      </section>
    );
  }
}

export default Cart;



